          </div>
          <button class="assessment-success-btn" @click="$router.push({ name: 'ManageSubscription' })">Manage Subscription</button>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserAssessment',
  data() {
    return {
      step: 1,
      words: [
        'Relaxed', 'Persuasive', 'Stable',
        'Charismatic', 'Individualistic', 'Optimistic',
        'Conforming', 'Methodical', 'Serious',
        'Friendly', 'Humble', 'Unrestrained',
        'Competitive', 'Docile', 'Restless'
      ],
      selectedWords: [[], []], // [step1, step2]
      submitted: false // Track if assessment is submitted
    }
  },
  methods: {
    goToNext() {
      this.step = 2;
    },
    goToBack() {
      this.step = 1;
    },
    submitAssessment() {
      this.submitted = true;
      this.$emit('submit', this.selectedWords);
    }
  }
}
</script>

