                  <span class="modal-icon"><i class="fas fa-user"></i></span>
                  <input type="text" placeholder="First Name" v-model="newMember.firstName" required />
                </div>
                <div class="modal-form-group">
                  <span class="modal-icon"><i class="fas fa-user"></i></span>
                  <input type="text" placeholder="Last Name" v-model="newMember.lastName" required />
                </div>
              </div>
              <div class="modal-form-row">
                <div class="modal-form-group">
