import { canAccess, ROLES } from '@/permissions.js'

const authMiddleware = {
  // Checks if a user is authenticated (role exists in localStorage)
  isAuthenticated() {
    const role = localStorage.getItem('role');
    return !!role && Object.values(ROLES).includes(role);
  },
  // Gets the current user's role from localStorage
  getRole() {
    return localStorage.getItem('role') || '';
  },
  // Checks if the current user can access a given route or component
  canAccess(type, name) {
    const role = localStorage.getItem('role');
    return canAccess(role, type, name);
  }
};

export default authMiddleware;
