import { createRouter, createWebHistory } from 'vue-router'
import Login from '@/components/auth/Login.vue'
import ThankYou from '@/components/auth/ThankYou.vue'
import Dashboard from '@/components/Common/Dashboard.vue'
import Organizations from '@/components/Common/Organizations.vue'
import Notifications from '@/components/Common/Notifications.vue'
import Leads from '@/components/Common/Leads.vue'
import TrainingResources from '@/components/Common/TrainingResources.vue'
import Assessments from '@/components/Common/Assessments.vue'
import MyOrganization from '@/components/Common/MyOrganization.vue'
import LeadCapture from '@/components/Common/LeadCapture.vue'
import SendAssessment from '@/components/Common/SendAssessment.vue'
import ScheduleDemo from '@/components/Common/ScheduleDemo.vue'
import ManageSubscription from '@/views/ManageSubscription.vue';
import GetNotifications from '@/views/GetNotifications.vue'
import { ROLES, PERMISSIONS, canAccess } from '@/permissions.js'

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/thankyou',
    name: 'ThankYou',
    component: ThankYou
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/organizations',
    name: 'Organizations',
    component: Organizations
  },
  {
    path: '/notifications',
    name: 'Notifications',
    component: Notifications
  },
  {
    path: '/leads',
    name: 'Leads',
    component: Leads
  },
  {
    path: '/training-resources',
    name: 'TrainingResources',
    component: TrainingResources
  },
  {
    path: '/assessments',
    name: 'Assessments',
    component: Assessments
  },
  {
    path: '/my-organization',
    name: 'MyOrganization',
    component: MyOrganization
  },
  {
    path: '/manage-subscription',
    name: 'ManageSubscription',
    component: ManageSubscription
  },
  {
    path: '/get-notification',
    name: 'GetNotification',
    component: GetNotifications,
    meta: { layout: 'main' }
  },
  {
    path: '/assessments/send-assessment',
    name: 'SendAssessmentAssessments',
    component: SendAssessment
  },

  {
    path: '/leads/:email',
    name: 'LeadDetail',
    component: () => import('@/components/Common/LeadDetail.vue'),
    props: true
  },
  {
    path: '/organizations/:orgName',
    name: 'OrganizationDetail',
    component: () => import('@/components/Common/OrganizationDetail.vue'),
    props: true
  },
  {
    path: '/organizations/billing-details',
    name: 'BillingDetails',
    component: () => import('@/components/Common/BillingDetails.vue')
  },
  {
    path: '/organizations/:orgName/edit',
    name: 'OrganizationEdit',
    component: () => import('@/components/Common/OrganizationEdit.vue'),
    props: true
  },
  {
    path: '/leads/lead-capture',
    name: 'LeadCapture',
    component: () => import('@/components/Common/LeadCapture.vue'),
  },
  {
    path: '/leads/send-assessment',
    name: 'SendAssessment',
    component: SendAssessment
  },
  {
    path: '/leads/schedule-demo',
    name: 'ScheduleDemo',
    component: ScheduleDemo
  },
  {
    path: '/leads/schedule-class-training',
    name: 'ScheduleClassTraining',
    component: () => import('@/components/Common/ScheduleClassTraining.vue')
  },
  {
    path: '/my-organization/members',
    name: 'MemberListing',
    component: () => import('@/components/Common/MemberListing.vue')
  },
  {
    path: '/assessments/:assessmentId/summary',
    name: 'AssessmentSummary',
    component: () => import('@/components/Common/AssessmentSummary.vue'),
    props: true
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.addRoute({
  path: '/:catchAll(.*)',
  redirect: '/dashboard'
});

//if no route matches, redirect to /dashboard
router.beforeEach((to, from, next) => {
  // Get user role from localStorage 
  const role = localStorage.getItem('role');

  // login / thank you for everyone
  if (to.path === '/' || to.path === '/thankyou') {
    return next();
  }

  // If no role, redirect to login
  if (!role || !PERMISSIONS[role]) {
    return next('/');
  }

  // Allow manage-subscription for all roles
  if (to.path === '/manage-subscription') {
    return next();
  }

  // Check if the role can access the route
  if (canAccess(role, 'routes', to.path)) {
    return next();
  } else {
    // unauthorized users to dashboard
    return next('/dashboard');
  }
});

export default router