<template>
  <div class="notifications-page">
    <div class="notifications-header">
    </div>
    <div class="notifications-content">
      <div class="notifications-controls">
        <input type="text" placeholder="Select Date" class="notifications-date" />
        <div class="notifications-tabs">
          <button :class="{active: tab==='unread'}" @click="tab='unread'">Unread</button>
          <button :class="{active: tab==='all'}" @click="tab='all'">All</button>
        </div>
        <button class="mark-all" @click="markAllRead">Mark all as read</button>
      </div>
      <div class="notifications-list">
        <div v-for="(item, idx) in paginatedNotifications" :key="idx" class="notification-item">
          <div class="notification-meta">
            <img src="@/assets/images/Logo.svg" class="notification-icon" />
            <span class="notification-date">{{ item.date }}</span>
          </div>
          <div class="notification-body">
            <span class="notification-title">Dolphin.</span>
            <span class="notification-text">{{ item.text }}</span>
          </div>
        </div>
      </div>
      <Pagination
        :pageSize="pageSize"
        :pageSizes="[5, 10, 20]"
        :currentPage="page"
        :totalPages="totalPages"
        :isNotifications="true"
        :showPageDropdown="showPageDropdown"
        @goToPage="goToPage"
        @selectPageSize="selectPageSize"
        @togglePageDropdown="togglePageDropdown"
      />
    </div>
  </div>
</template>

<script>
import Pagination from '@/components/layout/Pagination.vue';
export default {
  name: "GetNotification",
  components: { Pagination },
  data() {
    return {
      tab: 'unread',
      page: 1,
      pageSize: 10,
      showPageDropdown: false,
      notifications: [
        {
          date: "Jan 25, 2025 at 06:00 PM",
          text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
        },
        {
          date: "Jan 22, 2025 at 02:00 PM",
          text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
        },
        {
          date: "Jan 12, 2025 at 08:00 PM",
          text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
        },
        {
          date: "Jan 10, 2025 at 8:00 AM",
          text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
        },
        {
          date: "Jan 10, 2025 at 10:00 AM",
          text: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."
        }
      ]
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.notifications.length / this.pageSize) || 1;
    },
    paginatedNotifications() {
      const start = (this.page - 1) * this.pageSize;
      return this.notifications.slice(start, start + this.pageSize);
    }
  },
  methods: {
    markAllRead() {},
    prevPage() { if(this.page>1) this.page--; },
    nextPage() { if(this.page<this.totalPages) this.page++; },
    goToPage(n) { if(n >= 1 && n <= this.totalPages) this.page = n; },
    selectPageSize(size) { this.pageSize = size; this.page = 1; this.showPageDropdown = false; },
    togglePageDropdown() { this.showPageDropdown = !this.showPageDropdown; }
  }
}
</script>

