<template>
  <MainLayout>
    <div class="notifications-table-outer">
      <div class="notifications-table-container">
        <div class="notifications-header-row">
          <span class="notifications-header-title">Notification History</span>
          <button class="send-notification-btn" @click="showSendModal = true">
            <img src="@/assets/images/SendNotification.svg" alt="Send" class="send-notification-icon" />
            Send Notification
          </button>
        </div>
        <table class="notifications-table">
          <thead>
            <tr>
              <th class="rounded-th-left"><span class="notifications-th-content">Notification Title</span></th>
              <th><span class="notifications-th-content">Date &amp; Time</span></th>
              <th class="rounded-th-right"><span class="notifications-th-content">Action</span></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, idx) in paginatedNotifications" :key="idx">
              <td class="notifications-td-content">{{ item.title }}</td>
              <td class="notifications-td-content">{{ item.date }}</td>
              <td class="notifications-td-content">
                <button class="view-detail-btn">
                  <img src="@/assets/images/Detail.svg" alt="Detail" class="view-detail-icon" />
                  View Detail
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-if="showSendModal" class="send-notification-modal-overlay">
        <div class="send-notification-modal">
          <button class="modal-close-btn" @click="showSendModal = false">&times;</button>
          <div class="modal-title">Send Notifications</div>
          <div class="modal-desc">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</div>
          <textarea class="modal-textarea" placeholder="Type here"></textarea>
          <div class="modal-row">
            <div class="modal-field">
              <label>Select Organizations</label>
              <select><option>Select</option></select>
            </div>
            <div class="modal-field">
              <label>Select Admin</label>
              <select><option>Select</option></select>
            </div>
          </div>
          <div class="modal-row">
            <div class="modal-field">
              <label>Select Group</label>
              <select><option>Select</option></select>
            </div>
            <div class="modal-field modal-field-schedule">
              <label>Schedule</label>
              <div class="modal-schedule-fields">
                <input type="text" placeholder="MM/DD/YYYY" class="modal-date-input" />
                <input type="time" class="modal-time-input" />
              </div>
            </div>
          </div>
          <button class="modal-send-btn">Send Notification</button>
        </div>
      </div>
      <Pagination
        :pageSize="pageSize"
        :pageSizes="[10 , 25 , 100]"
        :showPageDropdown="showPageDropdown"
        :currentPage="currentPage"
        :totalPages="totalPages"
        :paginationPages="paginationPages"
        :isNotifications="true"
        @goToPage="goToPage"
        @selectPageSize="selectPageSize"
        @togglePageDropdown="showPageDropdown = !showPageDropdown"
      />
    </div>
  </MainLayout>
</template>

<script>
import MainLayout from '../layout/MainLayout.vue'
import Pagination from '../layout/Pagination.vue'

export default {
  name: 'Notifications',
  components: { MainLayout, Pagination },
  data() {
    return {
      showPageDropdown: false,
      showSendModal: false,
      pageSize: 10,
      currentPage: 1,
      notifications: [
        { title: 'Lorem Ipsum is simply', date: 'Jan 22, 2025 at 02:00 PM' },
        { title: 'Dummy text of the printing', date: 'Jan 22, 2025 at 02:00 PM' },
        { title: 'And typesetting industry.', date: 'Jan 12, 2025 at 01:15 PM' },
        { title: 'Lorem Ipsum text has been', date: 'Jan 10, 2025 at 01:00 PM' },
        { title: 'The industry standard', date: 'Jan 6, 2025 at 12:00 PM' },
        { title: 'Dummy text ever since', date: 'Dec 24, 2024 at 10:00 AM' },
        { title: 'When an unknown printer', date: 'Dec 24, 2024 at 10:00 AM' },
        { title: 'took a galley of type', date: 'Dec 24, 2024 at 10:00 AM' },
        { title: 'And scrambled it to make', date: 'Dec 24, 2024 at 10:00 AM' },
        { title: 'And scrambled it to make', date: 'Dec 15, 2024 at 4:40 PM' },
      ]
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.notifications.length / this.pageSize) || 1;
    },
    paginatedNotifications() {
      const start = (this.currentPage - 1) * this.pageSize;
      return this.notifications.slice(start, start + this.pageSize);
    },
    paginationPages() {
      // Show: 1, 2, ..., 10
      return [1, 2, '...', 10];
    }
  },
  methods: {
    goToPage(page) {
      if (page === '...' || page < 1 || page > this.totalPages) return;
      this.currentPage = page;
    },
    selectPageSize(size) {
      this.pageSize = size;
      this.currentPage = 1;
      this.showPageDropdown = false;
    }
  }
}
</script>

