<template>
  <nav class="navbar">
    <div class="navbar-left">
      <span class="navbar-page">{{ pageTitle }}</span>
    </div>
    <!-- Group bell and navbar-right together -->
    <div class="navbar-actions">
      <!-- Show bell only if not superadmin -->
      <router-link v-if="roleName !== 'superadmin'" to="/get-notification" style="display:flex;align-items:center;">
        <img src="@/assets/images/Group 7.svg" alt="Bell" style="height:36px;width:34px;vertical-align:middle;margin-right:8px;cursor:pointer;" />
      </router-link>
      <div class="navbar-right">
        <span class="navbar-avatar">{{ roleName.charAt(0) }}</span>
        <span class="navbar-username" v-show="!isVerySmallScreen">{{ displayName }}</span>
        <div class="navbar-dropdown-wrapper" ref="dropdownWrapper">
          <i class="fas fa-chevron-down" @click="toggleDropdown"></i>
          <transition name="fade">
            <div v-if="dropdownOpen" class="navbar-dropdown" ref="dropdown">
              <div class="navbar-dropdown-item">
                <i class="fas fa-user"></i>
                Profile
              </div>
              <div class="navbar-dropdown-item" @click="$router.push({ name: 'ManageSubscription' }); dropdownOpen = false">
                <i class="fas fa-credit-card"></i>
                Manage Subscriptions
              </div>
              <div class="navbar-dropdown-item" @click="confirmLogout">
                <i class="fas fa-sign-out-alt"></i>
                Logout
              </div>
            </div>
          </transition>
        </div>
      </div>
    </div>
    <div v-if="showLogoutConfirm" class="logout-confirm-overlay">
      <div class="logout-confirm-dialog">
        <div class="logout-confirm-title">Are you sure you want to logout?</div>
        <div class="logout-confirm-actions">
          <button class="logout-btn" @click="logout">Yes</button>
          <button class="cancel-btn" @click="showLogoutConfirm = false">Cancel</button>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import authMiddleware from '@/middleware/authMiddleware'

export default {
  name: 'Navbar',
  data() {
    return {
      dropdownOpen: false,
      showLogoutConfirm: false,
      roleName: authMiddleware.getRole(),
      isVerySmallScreen: false
    }
  },
  computed: {
    pageTitle() {
      const routeName = this.$route.name;
      if (routeName === 'LeadDetail') {
        let contact = this.$route.query.contact || '';
        if (contact) return `${contact} Details`;
        return 'Lead Details';
      }
      if (routeName === 'OrganizationDetail') {
        const orgName = this.$route.params.orgName || '';
        if (orgName) return `${orgName} Organization Details`;
        return 'Organization Details';
      }
      if (routeName === 'OrganizationEdit') {
        const orgName = this.$route.params.orgName || '';
        if (orgName) return `${orgName} Organization Details`;
        return 'Organization Details';
      }
      if (routeName === 'BillingDetails') {
        const orgName = this.$route.query.orgName || '';
        if (orgName) return `${orgName} Organization Details`;
        return 'Organization Details';
      }
      if (routeName === 'SendAssessment') {
        return 'Send Assessment';
      }
      if (routeName === 'ScheduleDemo') {
        return 'Schedule Demo';
      }
      if (routeName === 'Assessments') {
        return 'Assessments';
      }
      if (routeName === 'TrainingResources') {
        return 'Training & Resources';
      }
      if (routeName === 'MyOrganization') {
        return 'My Organization';
      }
      if (routeName === 'Notifications') {
        return 'Notification History';
      }
      if (routeName === 'Organizations') {
        return 'Organizations';
      }
      if (routeName === 'Leads') {
        return 'Leads';
      }
      if (routeName === 'ThankYou') {
        return 'Thank You';
      }
      if (routeName === 'Login') {
        return 'Login';
      }
      if (routeName === 'LeadCapture') {
        return 'Lead Capture';
      }
      if (this.$route.name === 'AssessmentSummaryPage') {
        const assessmentId = this.$route.params.assessmentId;
        return `Assessment ${assessmentId} Summary`;
      }
      return this.$route.name || '';
    },
    displayName() {
      return this.roleName;
    },
    role() {
      return authMiddleware.getRole();
    }
  },
  methods: {
    toggleDropdown() {
      this.dropdownOpen = !this.dropdownOpen;
    },
    handleClickOutside(event) {
      if (
        this.dropdownOpen &&
        this.$refs.dropdownWrapper &&
        !this.$refs.dropdownWrapper.contains(event.target)
      ) {
        this.dropdownOpen = false;
      }
    },
    confirmLogout(event) {
      event.stopPropagation();
      this.showLogoutConfirm = true;
      this.dropdownOpen = false;
    },
    logout() {
      this.showLogoutConfirm = false;
      this.$router.push({ name: 'Login' });
    },
    checkScreen() {
      this.isVerySmallScreen = window.innerWidth <= 420;
    }
  },
  mounted() {
    document.addEventListener('mousedown', this.handleClickOutside);
    window.addEventListener('resize', this.checkScreen);
    this.checkScreen();
  },
  beforeDestroy() {
    document.removeEventListener('mousedown', this.handleClickOutside);
    window.removeEventListener('resize', this.checkScreen);
  }
}
</script>

